  .slider__screen::before {
    content: "";
    width: 100%;
    height: 150px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 1;
  }
}
.slider__screen--before {
  width: 50%;
  z-index: 5;
  transition: width 1s ease;
}
.slider--before .slider__screen--before {
  width: 100%;
}
.slider--after .slider__screen--before {
  width: 0;
}
