  position: relative;
  z-index: 5;
}
.slider__handle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: white;
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  border: 2px solid #666666;
  transition: left 1s ease;
}
.slider--before .slider__handle {
  left: 100%;
}
.slider--after .slider__handle {
  left: 0;
}
